@import '../../../../assets/css/theme';
@import '../../../../assets/css/mixins';

.swap-block {
  width: 100%;
  border-radius: 10px;
  position: relative;
  z-index: 2;

  .sunswap-chose-token {
    background-color: $color-button-primary;
    border-radius: 5px;
    color: $color-button-primary-text;
    padding: 4px 10px 4px 10px;
    font-size: 14px;
    height: 36px;
  }
  .select-token-top {
    flex-direction: column;
    align-items: flex-start !important;
    > div:nth-child(1) {
      display: flex;
      justify-content: space-between;
    }
    > div:nth-child(2) {
      margin: 10px 0 0;
      display: flex;
      justify-content: start;
    }
  }

  .dragDown {
    margin: 0 10px 0 0;
    img {
      width: 20px;
      height: 20px;
      border-radius: 50%;
      border: 1px solid #ddd;
      background: #fff;
    }
    span {
      font-size: 16px;
      font-weight: 600;
      color: $color-text-default;
    }
  }

  .token-detail {
    width: 28px;
    height: 28px;
    border-radius: 50%;
  }

  .input-block {
    width: 100%;
    background: $color-input-bg;
    border-radius: 10px;
    padding: 14px 10px 14px 16px;

    .input-number {
      width: calc(100% - 86px);
      margin-right: 10px;
      flex: 1;
      input {
        width: 100%;
      }
    }

    input {
      font-size: 30px;
      font-family: Helvetica-Bold, Helvetica;
      font-weight: bold;
      background: $color-input-bg;
      color: $color-text-default;
      border: none;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      &:focus {
        outline: none;
      }
      &::placeholder {
        font-size: 30px;
        color: $color-text-default-3;
      }
    }
    input[placeholder] {
      font-size: 30px;
    }
  }

  .swap-balance {
    font-size: 14px;
    text-align: right;
    > span:nth-child(1) {
      color: $color-text-default;
      opacity: 0.5;
    }
    > span:nth-child(2) {
      color: $color-text-default;
    }
  }

  .max-btn {
    @include darkThemeOnly {
      background: #251f32;
      border: 1px solid rgba(255, 255, 255, 0.1);
      color: #fff;
    }
    @include lightThemeOnly {
      background: #decdff;
      border: 1px solid #722fff;
      color: #5a14e1;
    }
    border-radius: 15px;
    padding: 4px 14px;
    font-size: 12px;
    font-weight: 600;
    cursor: pointer;
    white-space: nowrap;
  }
}
.media-trade-router-pc {
  display: block;
}
.media-trade-router-mobile {
  display: none;
}
@media screen and (max-width: 768px) {
  .swap-block .input-block input::placeholder {
    font-size: 20px;
  }
  #root .media-trade-router-pc {
    display: none;
    &.flex {
      display: none;
    }
  }
  .media-trade-router-mobile {
    display: block;
  }
  .from-token-input {
    margin-bottom: 20px;
  }
}
.swap-block .input-block input.swap-to-value {
  color: $color-text-default-7;
}
