@import '../../../../assets/css/theme';
@import './inputBlock.scss';
@import '../../../../assets/css/var';
@import '../../../../assets/css/mixins';
.swap-content {
  background: transparent;
  &.ant-layout {
    background: transparent;
  }

  .flex-swap-block {
    width: 100%;
  }

  .swap-arrow {
    width: 46px;
    height: 46px;
    cursor: pointer;
    margin-bottom: 8px;
    @include darkThemeOnly {
      transform: rotate(-90deg);
      background: url('../../../../assets/images/swap-arrow-down.png') 100%/100% no-repeat;
    }
    @include lightThemeOnly {
      background: url('../../../../assets/images/arrow/arrow_solid_dark_right.png') 100% / 100% no-repeat;
      transform: rotate(0deg);
    }
    &:hover {
      @include darkThemeOnly {
        background: url('../../../../assets/images/swap-double-arrow.png') 100%/100% no-repeat;
      }
      @include lightThemeOnly {
        background: url('../../../../assets/images/arrow/arrow_double_dark_.png') 100% / 100% no-repeat;
      }
    }
  }

  .trade-router-loading {
    color: $color-text-default;
    font-size: 14px;
  }

  .price-loading-img {
    position: relative;
    &:after {
      animation: loadingPoint 2000ms infinite;
      position: absolute;
      content: '';
      left: 10px;
      top: 50%;
      transform: translateY(-50%);
      letter-spacing: 5px;
      font-size: 20px;
    }
  }

  @keyframes loadingPoint {
    25% {
      content: '·';
    }
    50% {
      content: '··';
    }
    75% {
      content: '···';
    }
    100% {
      content: '';
    }
  }

  .rates {
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $color-text-default-5;
    .token-rates {
      font-size: 14px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: $color-text-default;
      margin-left: 4px;
    }
    img {
      width: 20px;
      height: 20px;
    }
    @include mobile-only {
      .swap-token,
      .token-rates {
        font-size: 12px;
      }
    }
  }

  .add-recipient {
    font-size: 16px;
    font-weight: 600;
    color: $color-text-default;
    height: 28px;
    line-height: 28px;
    cursor: pointer;
    display: inline-block;
    &-remove {
      color: #ffac00;
    }
    .recipient-icon {
      width: 28px;
      height: 28px;
    }
  }

  .recipient-list {
    input {
      font-family: PingFangSC-Regular, PingFang SC;
      width: 100%;
      //background: $color-input-bg;
      //border-radius: 10px;
      //border: none;
      //padding: 10px 16px;
      //outline: none;
      //font-size: 14px;
      //font-weight: 400;
      //color: $white;
      //opacity: 0.5;
      //box-sizing: border-box;
    }
    .link {
      color: $color-a;
      font-size: 12px;
      margin-top: 10px;
    }
  }

  .sunswap-trade-router {
    font-size: 14px;
    font-weight: 600;
    color: $white;
    img {
      width: 18px;
      height: 18px;
      border-radius: 50%;
      border: 1px solid #ddd;
      background: #fff;
    }
    .trade-route {
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $color-text-default-5;
      .trans-router-list {
        color: $color-text-default;
      }
    }
    .tr-arrow {
      width: 8px;
      height: 8px;
      background: url(../../../../assets/images/homeTokens/ico_router_path.png) 0 0 no-repeat;
      background-size: 100% 100%;
      margin: 0 8px;
      @include lightThemeOnly {
        background: url('../../../../assets/images/homeTokens/ico_router_path_light.png') 0 0 no-repeat;
      }
    }
    li {
      &:last-child {
        .tr-arrow {
          display: none;
        }
      }
    }
  }

  .detail-item {
    color: $color-text-default-5;
    font-weight: 300;
    font-family: PingFangSC-Light, PingFang SC;
    font-size: 14px;
    > span {
      font-size: 14px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: $color-text-default;
      width: min-content;
      flex: 1;
    }
    .red {
      color: $error;
    }
    .orange {
      color: #ff8700;
    }
  }
  .action-btns {
    margin-top: 40px;
    @include mobile-only {
      margin-top: 30px;
    }
  }
}
.swap-content,
.swap-and-pool {
  .safe-amount-tip {
    font-size: 12px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $color-text-default;
    line-height: 20px;
    margin-top: 20px;
    .safe-amount-set-btn {
      color: $color-a;
      cursor: pointer;
    }
  }
}
.swap-and-pool .safe-amount-tip {
  margin-top: 0;
}

@media screen and (max-width: 576px) {
  .swap-content .swap-arrow {
    width: 30px;
    height: 30px;
    @include darkThemeOnly {
      transform: rotate(0deg);
      background: url('../../../../assets/images/swap-arrow-down.png') 100%/100% no-repeat;
    }
    @include lightThemeOnly {
      transform: rotate(90deg);
      background: url('../../../../assets/images/arrow/arrow_solid_dark_right.png') 100% / 100% no-repeat;
    }
    flex-shrink: 0;
    &:hover {
      @include darkThemeOnly {
        background: url('../../../../assets/images/swap-arrow-down.png') 100%/100% no-repeat;
      }
      @include lightThemeOnly {
        background: url('../../../../assets/images/arrow/arrow_solid_dark_right.png') 100% / 100% no-repeat;
      }
      transform: rotate(0deg);
    }
  }
}
