@import '../../../assets/css/theme';

@mixin greyText {
  font-family: AvenirNext-Regular;
  font-size: 12px;
  color: #999;
  text-align: left;
}

.v2-order-modal.ant-modal {
  .ant-modal-content {
    border-radius: 20px;
  }
  .ant-modal-footer {
    border: none;
    text-align: center;
  }
}
.v2-order-modal-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  line-height: 1em;
  padding: 10px 0;
  color: #333;
}
.v2-order {
  &-title {
    font-size: 12px;
  }
  &-grey-text {
    @include greyText;
    line-height: 18px;
    margin: 16px 0 30px;
  }
  &-info {
    background: $color-info-bg-light;
    border-radius: 5px;
    border: 1px solid rgba($color-text-default, 0.2);
    padding: 10px;
    font-weight: 600;
    color: $light-purple-btn;
    line-height: 24px;
    text-align: left;
  }
  &-logo {
    width: 16px;
    height: 16px;
    margin-right: 10px;
    vertical-align: middle;
    border-radius: 50%;
    border: 1px solid #ddd;
    background: #fff;
  }
  &-ver-mid {
    vertical-align: middle;
    color: #666;
  }
}
//v2-order
.v2-order {
  &-success {
    text-align: center;
  }
  &-success-icon {
    width: 80px;
    margin-top: 20px;
  }
  &-title-green {
    font-size: 20px;
    color: #16c378;
    margin: 24px 0 12px;
  }
  &-flex {
    display: flex;
    justify-content: center;
    align-items: flex-end;
  }
  &-add-value {
    font-size: 20px;
    color: #333333;
    letter-spacing: 0;
    text-align: right;
    display: inline-block;
    word-break: break-all;
  }
  &-add-token {
    font-size: 12px;
    color: #999;
    margin-top: 9px;
    margin-left: 3px;
  }
  &-plus {
    font-size: 14px;
    color: #5915e1;
    margin: 0 3px;
  }
  &-supplyDescTitle {
    margin-top: 14px;
    opacity: 0.8;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #999999;
  }

  &-amount {
    &-center {
      text-align: center;
    }
    &-num {
      line-height: 29px;
      font-size: 24px;
    }
    &-name {
      margin-top: 8px;
      line-height: 17px;
      font-size: 14px;
    }
  }

  &-line {
    text-align: center;
    &-add-value {
      margin-right: 4px;
      font-size: 20px;
      color: $color-text-default;
    }
    &-add-token {
      font-size: 14px;
      font-family: Helvetica-Bold, Helvetica;
      font-weight: 700;
      color: $color-text-default;
      line-height: 17px;
    }
    &-plus {
      margin: 0 14px;
      font-weight: 700;
      font-size: 20px;
      color: rgba(160, 156, 172, 1);
    }
    &-tip {
      @include greyText;
      margin: 10px 0 20px;
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      line-height: 18px;
      text-align: center;
    }
  }
}
.errorMsg {
  font-size: 14px;
  color: $color-warning;
  background: url(../assets/images/failed.svg) left no-repeat;
  background-size: 13px;
  padding-left: 18px;
  display: inline-block;
  margin: 0 auto 10px;
}

.v2-price-list {
  background: $color-info-bg;
  border-radius: 5px;
  border: 1px solid $color-border-secondary;
  padding: 0 16px;
  margin-bottom: 20px;
  margin-top: 20px;
  &-item {
    display: flex;
    align-items: center;
    border-bottom: 1px solid $color-border-secondary;
    padding: 16px 0;
    font-size: 16px;
    font-family: PingFangSC-Semibold, PingFang SC;
    font-weight: 600;
    color: $color-text-default;
    line-height: 22px;
    > img {
      width: 20px;
      height: 20px;
      margin-right: 10px;
      border-radius: 50%;
      border: 1px solid #ddd;
      background: #fff;
    }
    &:last-child {
      border: none;
    }
  }
  &-right {
    font-size: 14px;
    margin-left: auto;
    padding-left: 10px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
  }
}

.v2-share-container {
  @include greyText;
  margin-bottom: 20px;
  .v2-share-num {
    color: $color-text-default;
  }
}
