@import '../../../../../../assets/css/var.scss';
@import '../../../../../../assets/css/theme';
@import '../../../../../../assets/css/mixins';

$primary-btn-color: #5a14e1;
$primary-btn-hover-color: #5a14e180;

.sun-button-primary {
  background: $color-button-primary;
  border-color: $color-button-primary;
  color: $white;
  border-radius: 4px;
  height: 29px;
  width: 60px;
  line-height: 29px;
  text-align: center;
  cursor: pointer;
  font-size: 12px;
  &:hover {
    background: $color-button-primary;
    border-color: $color-button-primary;
    color: $white;
  }
  &:disabled,
  &.sun-button-weak {
    @include darkThemeOnly {
      background: $color-text-default-3;
      border-color: $color-text-default-3;
      color: $color-text-default-5;
    }
    @include lightThemeOnly {
      background: #cccccc;
      border-color: #cccccc;
      color: $white;
    }

    cursor: no-drop;

    &:hover {
      background: rgba(255, 255, 255, 0.3);
      border-color: rgba(255, 255, 255, 0.3);
      color: rgba(255, 255, 255, 0.5);
    }
  }
}
