@import '../../../assets/css/mixins';

.pool_list {
  @include common-content-width;
  margin: 60px auto 0;
  &_logo,
  &_logo_right {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 1px solid #ddd;
    background: #fff;
  }
  &_logo_right {
    transform: translateX(-6px);
  }
  &_btns {
    &_add,
    &_remove {
      width: 120px;
      height: 30px;
      background: $color-button-primary;
      border-radius: 4px;
      font-size: 14px;
      font-weight: 600;
      color: $color-text-default;
      line-height: 30px;
    }
    &_remove {
      background-color: transparent;
      margin-left: 20px;
      color: $color-a;
    }
  }
  &_total {
    font-size: 20px;
    font-weight: 600;
    color: $color-text-default;
    line-height: 28px;
    text-align: right;
    @include mobile-only {
      text-align: left;
    }
    &_label {
      margin-right: 10px;
      font-size: 14px;
      font-weight: 400;
      color: $color-text-default;
      line-height: 20px;
      opacity: 0.5;
    }
  }
}
