@import '../../../../assets/css/var.scss';
@import '../../../../assets/css/theme';
@import '../../../../assets/css/mixins';

.migrate-modal-wrap .ant-modal-content {
  padding: 30px 45px;
  border-radius: 20px;
  text-align: left;

  .common-tool {
    margin: 0;
    align-items: center;
  }

  .common-tool-title {
    color: #999;
    margin-right: 10px;
    width: calc(100% - 30px);
  }

  // .step1 {
  //   margin-top: -30px;
  // }

  .title {
    font-family: Helvetica Neue;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: #1a1a1a;
    height: 22px;
    margin-bottom: 25px;
    margin-top: 0 !important;
    position: relative;
    text-align: center;

    &.green {
      font-family: PingFangSC-Medium;
      font-size: 25px;
      color: #16c378;
      margin: 30px 0 15px !important;
    }
  }

  .desc {
    font-size: 12px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $color-text-default-5;
    line-height: 18px;
    text-align: left;
    margin-bottom: 20px !important;
  }
  .logo {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    border: 1px solid #ddd;
    background: #fff;
  }
  .migrate-modal-info {
    .logo {
      margin-right: 10px;
      width: 15px;
      height: 15px;
      border-radius: 50%;
      border: 1px solid #ddd;
      background: #fff;
    }
  }
  .lptokens {
    width: 100%;
    margin-bottom: 12px;
    text-align: left;
    display: flex;
    align-items: center;
    @include mobile-only {
      flex-wrap: wrap;
    }

    .logo:nth-child(2) {
      margin-left: -5px;
    }

    .name {
      font-size: 16px;
      font-family: Helvetica-Bold, Helvetica;
      font-weight: bold;
      color: $color-text-default;
      line-height: 19px;
      margin: 0 10px;
    }
    &-right-box {
      margin-left: auto;
      @include mobile-only {
        display: block;
        flex-basis: 100%;
        margin-left: 0;
        margin-top: 8px;
      }
    }
    &-right-name {
      font-weight: 400;
      color: $color-text-default-7;
      line-height: 20px;
    }
  }

  .version {
    width: 29px;
    height: 17px;
    background: $light-purple-btn;
    border-radius: 9px;
    font-size: 12px;
    font-family: PingFangSC-Semibold, PingFang SC;
    font-weight: 600;
    color: $purple-btn;
    line-height: 17px;
    display: inline-block;
    text-align: center;
  }

  .rate-line {
    margin-top: 10px;
    margin-bottom: 12px;
    display: flex;
    align-items: center;
  }

  .refunded {
    margin-bottom: 18px;
  }

  .arrow-wrap {
    margin: 10px 0 10px 10px;
  }

  .migrate-line {
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      width: 40px;
      height: 9px;
      margin: 0 14px;
    }
  }
  .migrate {
    &-add-value {
      font-family: Helvetica-Bold, Helvetica;
      font-weight: bold;
      color: $color-text-default;
      line-height: 29px;
      margin-right: 4px;
      @include responseFont(24px, 20px);
    }
    &-add-toke {
      font-size: 14px;
      font-family: Helvetica-Bold, Helvetica;
      font-weight: bold;
      color: $color-text-default;
      line-height: 17px;
    }
    &-plus {
      font-size: 20px;
      font-weight: bold;
      color: $color-text-default-5;
      line-height: 24px;
      margin: 0 10px;
    }
    &-add-version {
      margin-left: 10px;
      padding: 0 10px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: #722fff;
      line-height: 20px;
      background: #decdff;
      border-radius: 20px;
      @include responseFont();
    }
  }
  .step3-version {
    margin-left: 20px;
    width: 46px;
    height: 26px;
    line-height: 26px;
    vertical-align: 2px;
    font-size: 16px;
    border-radius: 13px;
    @include mobile-only {
      margin-left: 10px;
      width: 27px;
      height: 15px;
      line-height: 15px;
      font-size: 12px;
      border-radius: 10px;
    }
  }
}
