@import '../../../../assets/css/theme';
.liquidity_item {
  &_wrap {
    background: $color-card-bg-active;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  &_header {
    padding: 10px;
    display: flex;
    align-items: center;
  }
  &_name {
    font-size: 14px;
    font-weight: 600;
    color: $color-a;
    line-height: 20px;
    margin-left: 10px;
  }
  &_content {
    display: flex;
    padding: 0 10px 12px;
    justify-content: space-between;
  }
  &_info {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
    &:last-child {
      margin-bottom: 0;
    }
    &_info_label {
      font-size: 12px;
      font-weight: 400;
      color: $color-text-default-5;
      line-height: 17px;
      margin-right: 4px;
    }
    &_info_value {
      font-size: 12px;
      font-weight: 600;
      color: $color-text-default;
      line-height: 17px;
    }
  }
  &_footer {
    display: flex;
    align-items: stretch;
    justify-content: center;
    height: 35px;
    border-top: 1px solid $color-border-primary;
    &_btn {
      flex: 1;
      font-size: 14px;
      font-weight: 600;
      color: $color-a;
      border-right: 1px solid $color-border-primary;
      text-align: center;
      line-height: 35px;
      &:last-child {
        border: none;
      }
      &_disable {
        opacity: 0.5;
        cursor: no-drop;
      }
    }
  }
  &_migrate_btn {
    margin-left: auto;
    width: 110px;
    height: 25px;
    background: $color-button-default;
    border-radius: 13px;
    font-size: 12px;
    font-weight: 600;
    color: $color-button-default-text;
    line-height: 25px;
    cursor: pointer;
    text-align: center;
  }
  &_token_icon {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 1px solid #ddd;
    background: #fff;
  }
}
